import { useState } from "react";
import { useQuery } from "react-query";
import axios from "axios";
import { format } from "date-fns";
import Modal from 'react-modal';
import HoardingApplicationFullDetail from "./HoardingApplicationFullDetail";
import Loader from "../../Loader";
import AgencyNotification from "../AgencyNotification";
import BackToAgency from "../../BackToAgency";
import AdvertisementApiList from "../../../AdvertisementApiList";
import ApiHeaderAdvrt from "../../../ApiHeaderAdvrt";
import ErrorPage from "../../../ErrorPage";
import ListTableAdvert from "../../../ListTableAdvert";
import { useEffect } from "react";
import { toast, ToastContainer } from "react-toastify";
import BrandLoader from "../../../../../Components/Common/BrandLoader";
import CommonModal from "../../../../../Components/GlobalData/CommonModal";
import ServerErrorCard from "../../../ServerErrorCard";




const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        backgroundColor: 'white',
        border: 'none'
    },
};
Modal.setAppElement('#root');

function ViewAllHoardingApprovedList(props) {
    const [tableState, setTableState] = useState(false);
    const [listStatus, setlistStatus] = useState(false);
    const { api_getHoardingApprovedApplicationList, api_blacklistedApplication } = AdvertisementApiList()
    const [applicationDetail, setapplicationDetail] = useState()
    const [tableStatus, settableStatus] = useState(false);
    const [applicationType, setapplicationType] = useState()
    const [applicationNo, setapplicationNo] = useState()
    const [show, setshow] = useState(false)
    const [isLoading, setisLoading] = useState(false);
    const [erroState, seterroState] = useState(false);

    const [modalIsOpen, setIsOpen] = useState(false);
    const openModal = () => setIsOpen(true)
    const closeModal = () => setIsOpen(false)
    const afterOpenModal = () => { }

    const [documentList, setdocumentList] = useState()

    const showLoader = (val) => {
        setshow(val);
    }
    const notify = (toastData, type) => {
        toast.dismiss();
        if (type == 'success') {
            toast.success(toastData)
        }
        if (type == 'error') {
            toast.error(toastData)
        }
    };


    const COLUMNS = [
        {
            Header: "#",
            // accessor: 'ward_no'
            Cell: ({ row }) => <div className="pr-2">{row.index + 1}</div>,
        },
        {
            Header: "Application No",
            accessor: "application_no",
        },
        {
            Header: "Apply Date.",
            accessor: "application_date",
        },
        // {
        //     Header: "Payment Amount",
        //     accessor: "payment_amount",
        // },
        // {
        //     Header: "Firm Name",
        //     accessor: "bank_name",
        // },
        {
            Header: "Action",
            accessor: "id",
            Cell: ({ cell }) => (
                <>
                    <button
                        onClick={() => modalAction(cell.row.values.id, "Approve")}
                        className="bg-indigo-500 px-3 py-1 rounded leading-5 shadow-lg hover:shadow-xl hover:bg-indigo-700 
                hover:text-white text-white"
                    >
                        View
                    </button>

                    <button
                        onClick={() => handleBlacklisted(cell.row.values.id)}
                        className="bg-indigo-500 px-3 py-1 ml-2 rounded leading-5 shadow-lg hover:shadow-xl hover:bg-indigo-700 
hover:text-white text-white"
                    >
                        Blacklisted
                    </button>
                </>
            ),
        },
    ];

    const modalAction = (applicationId, applicationType) => {
        console.log("..............application id..............", applicationId)
        console.log("..............application type..............", applicationType)
        setapplicationNo(applicationId)
        setapplicationType(applicationType)
        openModal()
    }


    const onSuccess = (data) => {
        console.log("after fetching inbox list ....", data?.data?.data?.data);
        {
            data?.data?.data?.data?.length > 0 && setTableState(true);
        }
    };

    const getApplicationList = () => {
        seterroState(false)
        setisLoading(true)
        axios.post(api_getHoardingApprovedApplicationList, {}, ApiHeaderAdvrt())
            .then((res) => {
                console.log("res", res.data);
                if (res.data.status) {
                    setapplicationDetail(res.data.data);
                    settableStatus(true);
                    setisLoading(false)
                } else {

                }
            })
            .catch((err) => {
                console.log(err);
                seterroState(true)
                setisLoading(false)
            })
    }
    // const showLoader = (val) => {
    //     setshow(val);
    // }

    useEffect(() => {
        getApplicationList()
    }, [])

    const handleBlacklisted = (applicationId) => {
        const requestBody = {
            applicationId: applicationId,
        }
        console.log("request body in renew...12", applicationId)
        axios.post(`${api_blacklistedApplication}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('archived response', response.data)
                notify(" success", "success")
            })
            .catch(function (error) {
                console.log('errorrr.... ', error);
                notify("failed to submit", "error")
            })

    }

    if (isLoading) {
        return (
            <>
                <BrandLoader />
            </>
        )
    }
    if (erroState) {
        return (
            <CommonModal>
                <ServerErrorCard title="Server is busy" desc="Server is too busy to respond. Please try again later." buttonText="View Dashboard" buttonUrl="/propertyDashboard" />
            </CommonModal>
        )
    }

    return (
        <>
            <ToastContainer position="top-right"
                autoClose={2000} />
            <div className='container bg-white text-gray-600 h-screen mx-auto mt-16'>
                <div>
                    <div className="grid grid-cols-1 md:grid-cols-12 lg:grid-cols-12 gap-4 container mx-auto pt-4 ">
                        <div className="col-span-12 p-2">
                            <div className="flex flex-row bg-white mb-4 rounded leading-5 shadow-md shadow-violet-100 ">
                                <img src="https://cdn-icons-png.flaticon.com/512/3288/3288006.png" className="h-10 ml-3 mt-2" />
                                <h1 className="text-xl text-gray-700 font-semibold p-4">HOARDING APPROVED APPLICATIONS</h1>
                            </div>
                        </div>
                    </div>
                    <div className="grid grid-cols-1 p-2">
                        {/* {tableStatus && tableStatus?.data != null && */}
                        {tableStatus &&
                            <ListTableAdvert
                                assessmentType={false}
                                columns={COLUMNS}
                                dataList={applicationDetail?.data}
                            />
                        }
                        {/* {tableStatus && tableStatus?.data == null &&
                            <div className="text-xl font-semibold text-red-400 text-center">No Data</div>
                        } */}

                    </div>
                    <div className="grid grid-cols-1">
                        <Modal
                            isOpen={modalIsOpen}
                            onAfterOpen={afterOpenModal}
                            onRequestClose={closeModal}
                            style={customStyles}
                            contentLabel="Example Modal"
                        >
                            <div class=" rounded-lg shadow-xl border-2 border-gray-50 mx-auto px-0 " style={{ 'width': '80vw', 'height': '100%' }}>
                                <div className='h-[44rem] bg-white overflow-y-auto'>
                                    <HoardingApplicationFullDetail data={applicationNo} applicationType={applicationType} showLoader={showLoader} closeModal={closeModal} />
                                </div>
                            </div>
                        </Modal>
                    </div>
                </div>
            </div>
        </>
    );
}

export default ViewAllHoardingApprovedList;